import { RecursiveCharacterTextSplitter } from "langchain/text_splitter";
import { OpenAIEmbeddings } from "langchain/embeddings/openai";
import { MemoryVectorStore } from "langchain/vectorstores/memory";
import { RetrievalQAChain } from "langchain/chains";
import { ChatOpenAI } from "langchain/chat_models/openai";
import { PromptTemplate } from "langchain/prompts";
import { BaseRetrieverInterface } from "@langchain/core/retrievers";
import { BaseLanguageModelInterface } from "@langchain/core/language_models/base";
import { EmbeddingsInterface } from "@langchain/core/embeddings";

export class Responder {
  private static template = `Use the following pieces of context to answer the question at the end.
If the context does not contain the answer don't try to make up an answer.
{notice}
Context: {context}
Question: {question}`;

  private _model: BaseLanguageModelInterface;
  private _embeddings: EmbeddingsInterface;
  private _retriever: BaseRetrieverInterface;

  constructor(apiKey: string) {
    this._model = new ChatOpenAI({
      modelName: "gpt-3.5-turbo",
      temperature: 0,
      modelKwargs: { seed: 31216 },
      openAIApiKey: apiKey,
    });
    this._embeddings = new OpenAIEmbeddings({
      openAIApiKey: apiKey,
    });
  }

  async loadContext(knowledgeBase: string): Promise<void> {
    const kbSplitter = new RecursiveCharacterTextSplitter({
      chunkSize: 500,
      chunkOverlap: 0,
    });
    const kbDocs = await kbSplitter.createDocuments([knowledgeBase]);
    this._retriever = (await MemoryVectorStore.fromDocuments(kbDocs, this._embeddings)).asRetriever();
  }

  async response(question: string, notice: string): Promise<string> {
    if (this._retriever) {
      return RetrievalQAChain.fromLLM(this._model, this._retriever, {
        prompt: PromptTemplate.fromTemplate(Responder.template),
      })
        .call({ query: question, notice })
        .then((response) => this.normalizeYesNo(response.text));
    } else {
      return Promise.reject(Error("Response context not loaded"));
    }
  }

  private normalizeYesNo(response: string) {
    if (response.toLowerCase() == "yes.") return "Yes";
    else if (response.toLowerCase() == "no.") return "No";
    else return response;
  }
}
