import { Responder } from "../answers";

/* global localStorage, window, document, Office */

declare global {
  interface Window {
    responder: Responder;
  }
}

// The initialize function must be run each time a new page is loaded
Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("btnStoreKey").onclick = updateKey;
    document.getElementById("fileInput").onchange = loadConext;
    document.getElementById("openAIKey").oninput = openAIKeyChange;

    const storedApiKey = localStorage.getItem("openAIKey");
    if (storedApiKey) {
      window.responder = new Responder(storedApiKey);
      (document.getElementById("fileInput") as HTMLInputElement).disabled = false;
      (document.getElementById("openAIKey") as HTMLInputElement).placeholder = "key is already stored";
    }
  }
});

function updateKey() {
  const apiKey = (document.getElementById("openAIKey") as HTMLInputElement).value;
  if (apiKey) {
    window.responder = new Responder(apiKey);
    (document.getElementById("fileInput") as HTMLInputElement).disabled = false;
    localStorage.setItem("openAIKey", apiKey);
  }
}

function loadConext() {
  const myFile = document.getElementById("fileInput") as HTMLInputElement;
  const reader = new FileReader();
  reader.onload = () => window.responder.loadContext(reader.result as string);
  reader.readAsText(myFile.files[0]);
}

function openAIKeyChange() {
  const openAiKeyValue = (document.getElementById("openAIKey") as HTMLInputElement).value;
  (document.getElementById("btnStoreKey") as HTMLButtonElement).disabled = !openAiKeyValue;
}
